import React, { Dispatch, FC, SetStateAction } from "react";

import { CircularProgress } from "components";

import StepLine from "pages/Bonuses/components/Medal/components/Steps/StepLine";
import { replaceNumbers } from "utils/replaceString";
import { modalsLoaderColor } from "sass/styleVariables";

import MedalList from "./MedalList";
import { MedalType, Reward } from "..";
import css from "./style.module.scss";

interface Props {
  setOpen: Dispatch<SetStateAction<null | MedalType>>;
  list: MedalType[];
  rewards: {
    list: Reward[];
    collected: number;
  };
}

const List: FC<Props> = ({ setOpen, list, rewards }) => {
  return (
    <div className={css.root}>
      {!!rewards.list.length && (
        <StepLine rewards={rewards.list} active={rewards.collected} />
      )}

      <div className={css.listWrapper}>
        {rewards.list.map(({ reward, total_medal_count }) => (
          <p className={css.listElement}>
            {total_medal_count} Medals = {replaceNumbers(reward)} USDT
          </p>
        ))}
      </div>

      {!!list.length ? (
        <MedalList setOpen={setOpen} list={list} />
      ) : (
        <div className={css.loader}>
          <CircularProgress color={modalsLoaderColor} />
        </div>
      )}
    </div>
  );
};

export default List;
