import React, { useMemo, FC, useState, useEffect } from "react";

import { SelectOption, Select } from "components";

import { RakeBalance } from "../Rake/Activate/type";
import css from "./style.module.scss";
import { useCoins } from "hooks";

interface Props {
  options: RakeBalance[];
  value: string;
  onChange: (value: string) => void;
}

const RakeSelect: FC<Props> = ({ options, value: selectedValue, onChange }) => {
  const { coinsList } = useCoins("coinsList");

  const [renderValue, setRenderValue] = useState({
    img: "",
    name: "",
    amount: "",
  });

  const currenciesOptions = useMemo<SelectOption[]>(() => {
    return options?.map<SelectOption>(({ amount, name, symbol }) => {
      let img = "";
      const active = coinsList.find((value) => symbol === value.coin);
      if (active) {
        img = active.image;
      }
      return {
        value: symbol,
        label: `${name} ${amount}`,
        icon: img,
        name: name,
        amount: amount,
      };
    });
  }, [options, coinsList]);

  useEffect(() => {
    const active = coinsList?.find((value) => selectedValue === value.coin);
    const optionsActive = options?.find(
      (value) => selectedValue === value.symbol,
    );

    setRenderValue({
      img: active?.image || "",
      amount: optionsActive?.amount || "",
      name: optionsActive?.name || "",
    });
  }, [selectedValue, options]);

  return (
    <div className={css.root}>
      <Select
        value={selectedValue}
        options={currenciesOptions}
        onChange={(value) => onChange(value.toString())}
        className={css.select}
        labelClass={css.text}
        itemClass={css.selectItem}
        classes={{
          selectedAmount: css.label,
          expandIcon: css.label,
        }}
      />
    </div>
  );
};

export default RakeSelect;
