import React, { useEffect, useState } from "react";
import clsx from "clsx";

import { useFetch } from "hooks";
import { CircularProgress } from "components";

import { modalsLoaderColor } from "sass/styleVariables";

import { RakeFetchData, RakeTableData } from "../type";
import css from "./style.module.scss";

const maxElementsLength = 5;

const RakeRow = () => {
  const [levelData, setLevelData] = useState<RakeTableData[][]>([]);
  const [userLevel, setUserLevel] = useState({
    percentage: 0,
    level: 0,
  });

  const [rakeFetch, rakeData] = useFetch<RakeFetchData>("rakeback/get-info");

  useEffect(() => {
    rakeFetch();
  }, []);

  useEffect(() => {
    if (rakeData?.data) {
      const newData: RakeTableData[][] = [];
      const table = rakeData.data.table;
      let tab = 0;
      table.forEach((element, i) => {
        if (i % maxElementsLength === 0) {
          newData.push([]);
          tab = newData.length - 1;
        }
        newData[tab].push(element);
      });

      setLevelData(newData);
      setUserLevel({
        percentage: Number(rakeData.data.percentage),
        level: Number(rakeData.data.user_level),
      });
    }
  }, [rakeData.data]);

  return (
    <>
      {levelData.map((levels) => (
        <div className={css.box}>
          <div
            className={css.levels}
            style={{ gridTemplateColumns: `repeat(${levels.length}, 1fr)` }}>
            {levels.map(({ level }, i, src) => {
              let active = 0;
              let borderFix: "left" | "right" | "none" | "both" = "none";

              if (userLevel.level) {
                active = Number(userLevel.level);

                if (i !== 0) {
                  if (
                    i >= src.length - 1 ||
                    (src[i + 1] && active < Number(src[i + 1].level))
                  ) {
                    borderFix = "left";
                  } else if (active > Number(level)) {
                    borderFix = "both";
                  } else {
                    borderFix = "none";
                  }
                } else {
                  borderFix = "right";
                }
              }

              const borderStyle =
                borderFix === "both"
                  ? {
                      borderRadius: 0,
                    }
                  : borderFix === "right"
                  ? {
                      borderTopRightRadius: 0,
                      borderBottomRightRadius: 0,
                    }
                  : borderFix === "left"
                  ? {
                      borderTopLeftRadius: 0,
                      borderBottomLeftRadius: 0,
                    }
                  : {};
              return (
                <div
                  className={clsx(
                    {
                      [css.activeLevel]: active >= Number(level),
                    },
                    css.level,
                  )}
                  style={borderStyle}>
                  <p
                    className={clsx(css.levelText, {
                      [css.levelTextActive]: active >= Number(level),
                    })}>
                    Level {level}
                  </p>
                </div>
              );
            })}
          </div>
          <div
            className={css.percents}
            style={{ gridTemplateColumns: `repeat(${levels.length}, 1fr)` }}>
            {levels.map(({ percentage }) => {
              let active = 0;

              if (userLevel.percentage) {
                active = userLevel.percentage;
              }

              return (
                <div className={css.percentsWrapper}>
                  <div className={css.arrowWrapper}>
                    <div
                      className={clsx(css.percentArrow, {
                        [css.activePercentArrow]: active >= Number(percentage),
                      })}
                    />
                  </div>
                  <p
                    className={clsx(css.percentsText, {
                      [css.activePercentText]: active >= Number(percentage),
                    })}>
                    {percentage}%
                  </p>
                </div>
              );
            })}
          </div>
        </div>
      ))}

      {!rakeData.data && (
        <>
          <CircularProgress
            color={modalsLoaderColor}
            size={100}
            className={css.loader}
          />
        </>
      )}
    </>
  );
};

export default RakeRow;
