import React, { FC, useState } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { GetCoinImg } from "components";
import { replaceNumbers } from "utils";

import { TimeStamp } from "./TimeStamp";
import { BonusHistoryCode } from "..";
import { Scrollbar } from "components/Scrollbar";

import tableCss from "../table.module.scss";
import css from "./style.module.scss";

interface Props {
  codes: BonusHistoryCode[];
  height: number;
}

const Table: FC<Props> = ({ codes = [], height }) => {
  const intl = useGetLocale();
  const [root, setRoot] = useState<{
    element: HTMLSpanElement | null;
    index: number;
  }>({
    element: null,
    index: -1,
  });

  return (
    <div className={clsx(css.root)}>
      <Scrollbar style={{ height, overflowX: "hidden" }} withoutShadow>
        {codes.map((code, i) => {
          const {
            amount,
            bonus_code,
            claimed_by,
            coin,
            status,
            claimed_at = "claimed at october 21 1984",
          } = code;

          const statusText =
            status === "1"
              ? intl.formatMessage({
                  id: "bonusPage.modals.code.content.status.claimed",
                })
              : intl.formatMessage({
                  id: "bonusPage.modals.code.content.status.warning",
                });

          const isTimeStamp = claimed_at && status === "1" && root.index === i;

          return (
            <div className={tableCss.row} key={bonus_code}>
              <span
                className={clsx(
                  tableCss.element,
                  css.element,
                  css.hiddenText,
                  css.amountCoin,
                )}>
                <GetCoinImg coin={coin} className={css.coin} />
                {replaceNumbers(amount)}
              </span>
              <span className={clsx(tableCss.element, css.element)}>
                {bonus_code}
              </span>
              <span
                className={clsx(tableCss.element, css.element, css.hiddenText)}>
                {claimed_by}
              </span>
              <span
                onMouseOver={({ currentTarget }) => {
                  if (root.element !== currentTarget)
                    setRoot({
                      element: currentTarget,
                      index: i,
                    });
                }}
                onMouseLeave={() => {
                  setRoot({
                    element: null,
                    index: -1,
                  });
                }}
                className={clsx(
                  tableCss.element,
                  css.element,
                  css.statusElement,
                  {
                    [css.statusDone]: status === "0",
                  },
                )}>
                {statusText}
              </span>
              {/* {isTimeStamp && (
                <TimeStamp
                  anchor={isTimeStamp ? root.element : null}
                  value={claimed_at}
                />
              )} */}
            </div>
          );
        })}
      </Scrollbar>
    </div>
  );
};

export default Table;
