import React, { useEffect, useMemo, useState } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { useFetch, useModalLocation } from "hooks";

import { ReactComponent as Arrow } from "assets/icons/arrow-white.svg";

import List from "./List";
import Details from "./Details";
import css from "./style.module.scss";

export interface MedalType {
  id: string;
  medal_name: string;
  image_url: string;
  count: string;
  description: string;
}

export interface Reward {
  reward: string;
  total_medal_count: string;
}

export interface MedalFetch {
  medals: MedalType[];
  rewards: Reward[];
  medals_collected: number;
  message: string;
  success: boolean;
}

const Medal = () => {
  const intl = useGetLocale();
  const {
    params: { id: medalId },
    location,
    pushHistoryTo,
  } = useModalLocation("bonus");
  const [fetch, { data }] = useFetch<MedalFetch>("medal/get-all");

  const [medal, setMedal] = useState<null | MedalType>(null);
  const [medals, setMedals] = useState<MedalType[]>([]);
  const [rewards, setRewards] = useState<{
    list: Reward[];
    collected: number;
  }>({
    list: [],
    collected: 0,
  });

  const title = useMemo(
    () =>
      !!medal
        ? intl.formatMessage({
            id: "bonusPage.modals.medal.details",
          })
        : intl.formatMessage({
            id: "bonusPage.modals.medal.title",
          }),
    [medal],
  );

  const onClose = () => {
    if (medal) {
      setMedal(null);
      pushHistoryTo({
        search: { name: "page", value: "bonus" },
        searchParams: { bonus: "medal" },
      });
    }
  };

  useEffect(() => {
    fetch();
  }, []);

  useEffect(() => {
    if (data) {
      setMedals(data.medals);
      setRewards({
        collected: data.medals_collected,
        list: data.rewards,
      });
    }
  }, [data]);
  useEffect(() => {
    if (medal && medal.id) {
      pushHistoryTo({
        search: { name: "page", value: "bonus" },
        searchParams: { bonus: "medal", id: medal.id },
      });
    }
  }, [medal]);

  useEffect(() => {
    if (medalId) {
      const medal = medals.find(({ id }) => id === medalId);
      setMedal(medal || null);
    }
  }, [location, medals]);

  return (
    <div className={css.root}>
      <h4
        className={clsx(css.title, {
          [css.activeTitle]: !!medal,
        })}
        onClick={onClose}>
        {!!medal && <Arrow className={css.arrow} />}
        {title}
      </h4>
      {!medal ? (
        <List setOpen={setMedal} list={medals} rewards={rewards} />
      ) : (
        <Details
          medal={medal}
          amount={{
            max: medals.length,
            actual: rewards.collected,
          }}
        />
      )}
    </div>
  );
};

export default Medal;
