import React, { useCallback, useEffect, useState } from "react";
import { useGetLocale } from "providers";

import { Button, CircularProgress, notifyBarManager } from "components";
import { useCoins, useFetch } from "hooks";

import {
  RakeBalance,
  RakeBalanceData,
  RakeWithdrawAnswer,
} from "../Activate/type";
import css from "./style.module.scss";
import RakeSelect from "../../RakeSelect";
import { modalsLoaderColor } from "sass/styleVariables";
import { fetchBase } from "utils";

const CoinSelect = () => {
  const intl = useGetLocale();
  const { selectedCoin } = useCoins("selectedCoin");

  const [rakeCoins, setRakeCoins] = useState<RakeBalance[]>([
    { name: "GEM", symbol: "GEM", amount: "0.00000000" },
    { name: "Tron", symbol: "TRX", amount: "0.00000000" },
    { name: "Nano", symbol: "NANO", amount: "0.00000000" },
    { name: "Tether", symbol: "USDT", amount: "0.00000000" },
    { name: "Feyorra", symbol: "FEY", amount: "0.00000000" },
    { name: "Bitcoin", symbol: "BTC", amount: "0.00000000" },
    { name: "Ethereum", symbol: "ETH", amount: "0.00000000" },
    { name: "Litecoin", symbol: "LTC", amount: "0.00000000" },
    { name: "Dogecoin", symbol: "DOGE", amount: "0.00000000" },
  ]);
  const [activeRakeCoin, setActiveCoin] = useState<string>(
    selectedCoin?.coin || "",
  );

  const selectChange = (value: string) => setActiveCoin(value || "");

  const getCoins = async () => {
    const rakeBalance = await fetchBase<RakeBalanceData>(
      "rakeback/get-balances",
    );
    if (rakeBalance.data) {
      setRakeCoins(rakeBalance.data.balances);
    }
  };
  useEffect(() => {
    getCoins();
  }, []);

  const [balanceFetch, balanceData] = useFetch<RakeWithdrawAnswer>(
    "rakeback/withdraw-balance",
  );
  const handleRakeWithdraw = useCallback(async () => {
    const result = await balanceFetch({
      variables: {
        coin: activeRakeCoin,
      },
    });
    if (result.data) {
      const isError = !!result.error || !result.data?.success;
      notifyBarManager[isError ? "error" : "success"]({
        message: result.data.message,
        timeOut: 4000,
      });
    }

    getCoins();
  }, [activeRakeCoin]);

  return (
    <>
      <RakeSelect
        options={rakeCoins}
        value={activeRakeCoin}
        onChange={selectChange}
      />

      <Button
        disabled={balanceData.loading}
        classes={{
          root: css.button,
        }}
        onClick={handleRakeWithdraw}>
        {balanceData.loading ? (
          <CircularProgress
            color={modalsLoaderColor}
            className={css.loader}
            size={50}
          />
        ) : (
          intl.formatMessage({
            id: "bonusPage.modals.rake.activate.button",
          })
        )}
      </Button>
    </>
  );
};

export default CoinSelect;
