import React, { FC } from "react";
import { Icon } from "components";

import css from "./style.module.scss";

interface Props {
  title: string;
  onClose: () => void;
}

const Header: FC<Props> = ({ title, onClose }) => {
  return (
    <div className={css.root}>
      <h1 className={css.title}>{title}</h1>
      <Icon
        variant="close"
        className={css.icon}
        onClick={onClose}
        fontSize={10}
      />
    </div>
  );
};

export default Header;
