import { useCoins } from "hooks";
import React, { useEffect, useState, FC } from "react";

import { RakeBalance } from "../../../Rake/Activate/type";
import RakeSelect from "../../../RakeSelect";

interface Props {
  coinChange: (value: string) => void;
  activeCoin: string;
}

const CoinSelect: FC<Props> = ({ coinChange, activeCoin }) => {
  const { coinsList, selectedCoin } = useCoins(["coinsList", "selectedCoin"]);

  const [rakeCoins, setRakeCoins] = useState<RakeBalance[]>([
    { name: "GEM", symbol: "GEM", amount: "0.00000000" },
    { name: "Tron", symbol: "TRX", amount: "0.00000000" },
    { name: "Nano", symbol: "NANO", amount: "0.00000000" },
    { name: "Tether", symbol: "USDT", amount: "0.00000000" },
    { name: "Feyorra", symbol: "FEY", amount: "0.00000000" },
    { name: "Bitcoin", symbol: "BTC", amount: "0.00000000" },
    { name: "Ethereum", symbol: "ETH", amount: "0.00000000" },
    { name: "Litecoin", symbol: "LTC", amount: "0.00000000" },
    { name: "Dogecoin", symbol: "DOGE", amount: "0.00000000" },
  ]);

  useEffect(() => {
    if (selectedCoin) {
      coinChange(selectedCoin.coin);
    }
  }, [selectedCoin]);

  useEffect(() => {
    if (!!coinsList.length) {
      const newRake = coinsList.map((rake) => {
        const coin = rakeCoins.find((coin) => rake.coin === coin.symbol);

        return {
          symbol: rake.coin,
          name: coin?.name || rake.coin,
          amount: rake.balance,
        };
      });

      setRakeCoins(newRake);
    }
  }, [coinsList]);

  const selectChange = (value: string) => coinChange(value || "");

  return (
    <RakeSelect
      options={rakeCoins}
      value={activeCoin}
      onChange={selectChange}
    />
  );
};

export default CoinSelect;
