import React, { ChangeEvent, FC, FocusEvent, useMemo } from "react";

import { Button } from "components";
import { useCoins } from "hooks";

import { replaceNumbers } from "utils/replaceString";

import css from "./style.module.scss";

interface Props {
  value: string;
  onChange: (value: string) => void;
  coin: string;
}

const Amount: FC<Props> = ({ onChange, value, coin }) => {
  const { coinsList } = useCoins("coinsList");

  const selectedCoin = useMemo(
    () => coinsList.find((value) => value.coin === coin),
    [coin, coinsList],
  );

  const handleInputChange = (event: ChangeEvent<HTMLInputElement>) => {
    const text = event.target.value;
    if (Number(text) || Number(text) === 0) {
      onChange(text);
    }
  };

  const handleInputBlur = (event: FocusEvent<HTMLInputElement>) =>
    onChange(replaceNumbers(Number(event.target.value).toFixed(8)));

  const handleMin = () => {
    onChange("0.00000001");
  };

  const handleMax = () => {
    onChange(selectedCoin?.balance || "0.00000001");
  };

  const handlePercentChange = (value: 0.25 | 0.5) => {
    let newValue = "0.00000001";
    if (selectedCoin) {
      newValue = (Number(selectedCoin.balance) * value).toFixed(8);
    }
    onChange(newValue);
  };

  const handle25p = () => {
    handlePercentChange(0.25);
  };

  const handle50p = () => {
    handlePercentChange(0.5);
  };

  return (
    <div className={css.root}>
      <div className={css.inputWrapper}>
        <p className={css.label}>Amount</p>
        <input
          className={css.input}
          onChange={handleInputChange}
          value={value}
          onBlur={handleInputBlur}
        />
      </div>
      <div className={css.buttonWrapper}>
        <Button onClick={handleMin}>min</Button>
        <Button onClick={handle25p}>25%</Button>
        <Button onClick={handle50p}>50%</Button>
        <Button onClick={handleMax}>max</Button>
      </div>
    </div>
  );
};

export default Amount;
