import React, { FunctionComponent } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { Modal } from "components";
import { ModalsProps } from "config/routes";

import { Header, Rake, Code, Medal } from "./components";
import css from "./style.module.scss";
import { useModalLocation } from "hooks";

export type BonusTypes = "rake" | "bonuscode" | "medal";
interface ModalParams {
  class: string;
  title: string;
  Component: null | ((props?: any) => JSX.Element);
  width?: number;
}

const BonusModal: FunctionComponent<ModalsProps> = ({ isOpen }) => {
  const intl = useGetLocale();

  const {
    params: { bonus, id },
    close: closeModal,
  } = useModalLocation("bonus");

  const close = () => {
    closeModal();
  };

  const getModalParams = () => {
    const result: ModalParams = {
      class: "",
      title: "",
      Component: null,
    };

    switch (bonus) {
      case "bonuscode":
        result.class = css.rake;
        result.title = intl.formatMessage({
          id: "bonusPage.modals.code.title",
        });
        result.Component = Code;
        break;

      case "rake":
        result.class = css.bonus;
        result.title = intl.formatMessage({
          id: "bonusPage.modals.rake.title",
        });
        result.Component = Rake;
        break;

      default:
      case "medal":
        result.class = css.medal;
        result.title = "";
        result.Component = Medal;
        //@ts-ignore
        result.width = "fit-content";
        break;
    }

    return result;
  };

  const params = getModalParams();

  return (
    <Modal
      open={isOpen}
      width={params.width || ""}
      onClose={close}
      className={css.paper}>
      <div className={clsx(css.root, params.class)}>
        <Header title={params.title} onClose={close} />
        <div className={css.scrollBox}>
          {params.Component && <params.Component />}
        </div>
      </div>
    </Modal>
  );
};

export default BonusModal;
