import React, { Dispatch, FC, SetStateAction } from "react";

import line_URL from "assets/medals/textLine.svg";

import { MedalType } from "../..";
import css from "./style.module.scss";

interface Props {
  setOpen: Dispatch<SetStateAction<null | MedalType>>;
  list: MedalType[];
}

const MedalList: FC<Props> = ({ setOpen, list }) => {
  return (
    <div className={css.root}>
      {list.map((medal) => {
        const { count, description, image_url, medal_name, id } = medal;

        const onClick = () => {
          setOpen(medal);
        };
        return (
          <div className={css.card} onClick={onClick} key={id}>
            <div className={css.cardWrapper}>
              <img src={image_url} alt={medal_name} className={css.medalImg} />

              <h3 className={css.medalName}>{medal_name}</h3>
              <p className={css.medalDescription}>{description}</p>

              <span className={css.numberWrapper}>
                <img src={line_URL} alt={count} className={css.numberImg} />
                <p className={css.numberElement}>{count}</p>
              </span>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default MedalList;
