import React, { FC } from "react";
import clsx from "clsx";

import {
  StepLine_1,
  StepLine_2,
  StepLine_3,
} from "pages/Bonuses/assets/medals";
import { Reward } from "pages/ModalPages/pages/Bonuses/components/Medal";
import css from "./style.module.scss";

const steps = ["Start!", "5 Medals", "10 Medals", "15 Medals", "20 Medals"];

interface Props {
  rewards?: Reward[];
  active?: number;
}

const StepLine: FC<Props> = ({ rewards, active }) => {
  const stepList: string[] = rewards
    ? [
        "Start!",
        ...rewards.map(({ total_medal_count }) => {
          return `${total_medal_count} Medals!`;
        }),
      ]
    : steps;

  return (
    <div className={css.root}>
      {stepList.map((value, i, src) => {
        const StepImg =
          i === 0 ? StepLine_1 : i === src.length - 1 ? StepLine_3 : StepLine_2;

        const isDisable =
          i > 0 && rewards && rewards[i - 1] && (active || active === 0)
            ? Number(rewards[i - 1].total_medal_count) > active
            : i > 0
            ? true
            : false;

        return (
          <div className={css.wrapper} key={value + i}>
            <p
              className={clsx(css.text, {
                [css.disableText]: isDisable,
              })}>
              {value}
            </p>

            <StepImg
              className={clsx(css.img, {
                [css.disableImg]: isDisable,
              })}
            />
          </div>
        );
      })}
    </div>
  );
};

export default StepLine;
