import React, { FC, useEffect, useRef, useState } from "react";

import { useWindowSize } from "providers";

import line_URL from "assets/medals/textLine.svg";

import css from "./style.module.scss";
import { MedalType } from "..";

interface Props {
  medal: MedalType;
  amount: {
    actual: number;
    max: number;
  };
}

const Details: FC<Props> = ({ medal, amount }) => {
  const { width } = useWindowSize();

  const [processData, setProcessData] = useState(amount);
  const [processWidth, setProcessWidth] = useState(10);

  const processRef = useRef<HTMLDivElement>(null);

  const { count, description, image_url, medal_name } = medal;

  useEffect(() => {
    if (processRef.current) {
      const percent = (processData.actual * 100) / processData.max;

      const calcWidth = processRef.current.clientWidth * (percent / 100);
      setProcessWidth(calcWidth);
    }
  }, [processRef, width, processData]);

  return (
    <div className={css.root}>
      <img src={image_url} alt={medal_name} className={css.img} />

      <h2 className={css.title}>{medal_name}</h2>
      <p className={css.description}>{description}</p>

      <div className={css.processWrapper}>
        <p className={css.processNumber}>
          <span className={css.processNumFull}>{processData.actual}</span>/
          {processData.max}
        </p>

        <div className={css.processUnit} ref={processRef}>
          <div
            className={css.processFull}
            style={{
              maxWidth: processWidth,
            }}
          />
        </div>
      </div>

      <span className={css.imgWrapper}>
        <img src={line_URL} alt={count} className={css.imgElement} />
        <p className={css.imgNumber}>{count}</p>
      </span>
    </div>
  );
};

export default Details;
