import React from "react";
import { useGetLocale } from "providers";

import RakeRow from "./Row";
import CoinSelect from "../CoinSelect";
import css from "./style.module.scss";

const Activate = () => {
  const intl = useGetLocale();

  return (
    <div className={css.root}>
      <h4 className={css.text}>
        {intl.formatMessage({
          id: "bonusPage.modals.rake.activate.active",
        })}
      </h4>
      <RakeRow />
      <CoinSelect />
    </div>
  );
};

export default Activate;
