import React, { ChangeEvent, useCallback, useState } from "react";
import { useGetLocale } from "providers";

import { Button, Input, notifyBarManager } from "components";
import { fetchBase } from "utils";

import css from "./style.module.scss";

export interface RedeemResponse {
  amount: string;
  balance: string;
  coin: string;
  message: string;
  success: boolean;
}

const RedeemCode = () => {
  const intl = useGetLocale();

  const [code, setCode] = useState("");

  const handleSetCode = useCallback((event: ChangeEvent<HTMLInputElement>) => {
    setCode(event.target.value.toUpperCase());
  }, []);

  const handleRedeem = useCallback(async () => {
    const response = await fetchBase<RedeemResponse>("bonuscode/redeem", {
      variables: {
        bonus_code: code,
      },
    });

    if (response.data) {
      notifyBarManager[response.data.success ? "success" : "error"]({
        message: response.data.message,
        timeOut: 4000,
      });
    }
  }, [code]);

  return (
    <div className={css.redeemWrapper}>
      <Input
        classes={{
          input: css.redeemInput,
          root: css.redeemInputRoot,
        }}
        placeholder={intl.formatMessage({
          id: "bonusPage.modals.code.content.redeem.placeholder",
        })}
        value={code}
        onChange={handleSetCode}
      />
      <Button
        onClick={handleRedeem}
        classes={{
          root: css.redeemButtonRoot,
          label: css.redeemLabel,
        }}>
        {intl.formatMessage({
          id: "bonusPage.modals.code.content.redeem.button",
        })}
      </Button>
    </div>
  );
};

export default RedeemCode;
