import React, { useEffect, useState, FC } from "react";

import { Pagination } from "components";

import Header from "./Header";
import Table from "./Table";
import css from "./style.module.scss";
import { fetchBase } from "utils";

export interface BonusHistoryCode {
  amount: string;
  bonus_code: string;
  claimed_by: string;
  claimed_at?: string | number;
  coin: string;
  status: string;
}

interface Fetch {
  current_page: number;
  message: string;
  success: boolean;
  total_pages: number;

  codes: BonusHistoryCode[];
}
interface Props {
  open: boolean;
  height: number;
}

const HistoryTable: FC<Props> = ({ open, height }) => {
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState({
    current: 1,
    amount: 1,
  });
  const [codes, setCodes] = useState<BonusHistoryCode[]>([]);

  useEffect(() => {
    const getTable = async () => {
      setLoading(true);
      const { data } = await fetchBase<Fetch>("bonuscode/history", {
        variables: {
          page: page.current,
        },
      });

      if (data) {
        setCodes(data.codes);
        setPage({
          ...page,
          amount: data.total_pages,
        });
        setLoading(false);
      }
    };

    if (open) {
      getTable();
    }
  }, [open, page.current]);

  const handleChangePage = (value: number) => {
    setPage({
      ...page,
      current: value,
    });
  };
  const handlePagination = (page: number) => handleChangePage(page);
  const getTableHeight = () => {
    let defaultHeight = height - 33 - 52;

    if (!defaultHeight) {
      //@ts-ignore
      defaultHeight = "50vh";
    }

    return defaultHeight;
  };

  return (
    <div className={css.root}>
      <Header />
      <Table codes={codes} height={getTableHeight()} />
      <Pagination
        page={page.current}
        pageAmount={page.amount}
        maxButtonVisible={4}
        showFirstLastPage
        disabled={loading}
        onChange={handlePagination}
        classes={{
          root: css.paginationRoot,
          button: css.paginationButton,
          activePage: css.paginationActive,
        }}
      />
    </div>
  );
};

export default HistoryTable;
