import React from "react";
import { useGetLocale } from "providers";

import Activate from "./Activate";
import css from "./style.module.scss";

const Rake = () => {
  const intl = useGetLocale();

  return (
    <div className={css.root}>
      <p className={css.subTitle}>
        {intl.formatMessage({
          id: "bonusPage.modals.rake.subTitle",
        })}
      </p>

      <Activate />
    </div>
  );
};

export default Rake;
