import React, { useCallback, useEffect, useRef, useState } from "react";
import { useGetLocale } from "providers";

import HistoryTable from "./HistoryTable";
import CreateCode from "./CreateCode";
import RedeemCode from "./RedeemCode";
import css from "./style.module.scss";
import { useWindowSize } from "providers";

const Code = () => {
  const intl = useGetLocale();
  const { height, width } = useWindowSize();

  const [isHistoryShow, setIsHistoryShow] = useState(false);
  const [bonusSizes, setBonusSizes] = useState({
    width: 0,
    height: 0,
  });
  const [bonusElement, setBonusElement] = useState<HTMLDivElement | null>(null);
  const [activeCoin, setActiveCoin] = useState("btc");
  const [amountValue, setAmountValue] = useState("0.00000001");

  const coinChange = (value: string) => setActiveCoin(value);
  const handleChangeHistory = () => setIsHistoryShow(!isHistoryShow);
  const historyOpen = () => setIsHistoryShow(true);
  const amountChange = (value: string) => {
    setAmountValue(value);
  };

  useEffect(() => {
    const setDefaultSizes = () => {
      const sizes = {
        height: "70vh",
        width: "100%",
      };
      //@ts-ignore
      setBonusSizes(sizes);
    };

    if (bonusElement && width > 600) {
      let height = 0;
      let width = 0;

      height = bonusElement.clientHeight;
      width = bonusElement.clientWidth;

      if (height && width) {
        if (bonusSizes.height !== height || bonusSizes.width !== width) {
          setBonusSizes({
            height,
            width,
          });
        }
      } else {
        setDefaultSizes();
      }
    } else {
      setDefaultSizes();
    }
  }, [height, width, bonusElement, isHistoryShow]);

  return (
    <div className={css.root}>
      <div
        className={css.contentWrapper}
        ref={(node) => {
          setBonusElement(node);
        }}
        style={
          isHistoryShow
            ? {
                transform: "scaleY(0)",
                position: "absolute",
                top: 0,
              }
            : {}
        }>
        <p className={css.subTitle}>
          {intl.formatMessage({
            id: "bonusPage.modals.code.subTitle",
          })}
        </p>

        <RedeemCode />

        <CreateCode
          activeCoin={activeCoin}
          amountValue={amountValue}
          amountChange={amountChange}
          coinChange={coinChange}
          showHistory={historyOpen}
        />
      </div>
      <div
        className={css.tableWrapper}
        style={
          isHistoryShow
            ? {
                position: "static",
                transform: "scaleY(1)",
                height: bonusSizes.height,
                width: bonusSizes.width,
              }
            : {
                height: bonusSizes.height,
                width: bonusSizes.width,
              }
        }>
        <HistoryTable open={isHistoryShow} height={bonusSizes.height} />
      </div>

      <span className={css.linkText} onClick={handleChangeHistory}>
        {isHistoryShow
          ? intl.formatMessage({
              id: "bonusPage.modals.code.content.menu",
            })
          : intl.formatMessage({
              id: "bonusPage.modals.code.content.history",
            })}
      </span>
    </div>
  );
};

export default Code;
