import React, { FC } from "react";
import { useGetLocale } from "providers";

import { Button, CircularProgress, notifyBarManager } from "components";
import { coinsManager, useFetch } from "hooks";

import { modalsLoaderColor } from "sass/styleVariables";

import Amount from "./Amount";
import CoinSelect from "./Select";
import css from "./style.module.scss";

interface CodeFetchResponse {
  balance: string;
  bonus_code: string;
  coin: string;

  message: string;
  success: boolean;
}

interface Props {
  activeCoin: string;
  amountValue: string;
  coinChange: (value: string) => void;
  amountChange: (value: string) => void;

  showHistory: () => void;
}

const CreateCode: FC<Props> = ({
  activeCoin,
  coinChange,
  amountChange,
  amountValue,
  showHistory,
}) => {
  const intl = useGetLocale();

  const [createCode, { loading }] =
    useFetch<CodeFetchResponse>("bonuscode/create");

  const handleCreateBonus = async () => {
    const response = await createCode({
      variables: {
        amount: amountValue,
        coin: activeCoin,
      },
    });

    if (response.data) {
      coinsManager.coinValueUpdate(response.data.balance, response.data.coin);
      notifyBarManager[response.data.success ? "success" : "error"]({
        message: response.data.message,
        timeOut: 4000,
      });
      if (response.data.success) {
        showHistory();
      }
    }
  };

  return (
    <>
      <div className={css.box}>
        <p className={css.mainText}>
          {intl.formatMessage({
            id: "bonusPage.modals.code.content.create",
          })}
        </p>
        <CoinSelect activeCoin={activeCoin} coinChange={coinChange} />

        <Amount onChange={amountChange} value={amountValue} coin={activeCoin} />
      </div>

      <div className={css.buttonWrapper}>
        <Button
          classes={{
            root: css.boxRoot,
            label: css.boxLabel,
          }}
          disabled={loading}
          onClick={handleCreateBonus}>
          {loading ? (
            <CircularProgress color={modalsLoaderColor} />
          ) : (
            intl.formatMessage({
              id: "bonusPage.modals.code.content.button",
            })
          )}
        </Button>
      </div>
    </>
  );
};

export default CreateCode;
