import React from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import tableCss from "../table.module.scss";
import css from "./style.module.scss";

const Header = () => {
  const intl = useGetLocale();

  return (
    <div className={clsx(css.root, tableCss.row)}>
      <span className={tableCss.element}>
        {intl.formatMessage({
          id: "bonusPage.modals.code.content.table.amount",
        })}
      </span>
      <span className={tableCss.element}>
        {intl.formatMessage({
          id: "bonusPage.modals.code.content.table.code",
        })}
      </span>
      <span className={tableCss.element}>
        {intl.formatMessage({
          id: "bonusPage.modals.code.content.table.claimed",
        })}
      </span>
      <span className={tableCss.element}>
        {intl.formatMessage({
          id: "bonusPage.modals.code.content.table.status",
        })}
      </span>
    </div>
  );
};

export default Header;
